@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
/* GLOBAL STYLES */

body,
html {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-style: normal;
}

.heading {
  font-size: 36px;
  font-weight: 500;
  color: white;
  margin: 0;
}
.para {
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.04em;
}

 .nav-links a{
  color: white;
  font-size: 16px;
  font-weight: 500;
  line-height: 35px;
  padding: 10px;
}
.nav-links li{
  list-style: none;
}
.navbar img{
  width:204px;
  margin-top :7px
}
.nav-links {
  margin-top: 4px;
  display: flex;
  float: right;
}
.ant-layout-header{
  background: #0094FF;
  height: 41px;
  line-height: 24px;
}




.about-sec{
  height: 50vh;
 }
.ant-layout-content {
  background-color: white;
}
.About-content {
  text-align: center;
  height: 48vh;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(43 41 41)), to(rgba(12, 10, 10, 0))),url(/dtangeeda/hospitals-ui/static/media/contactbg.a9f1b1f6.png);
  background-image: linear-gradient(rgb(43 41 41), rgba(12, 10, 10, 0)),url(/dtangeeda/hospitals-ui/static/media/contactbg.a9f1b1f6.png);
  background-size: cover;
}
.about-para{
  width: 50%;
  margin: auto;
}
.about-heading {
  margin-top: 80px;
  color: white;
}

.footer-icons {
  font-size: 20px;
}
.footer-icons :hover {
  cursor: pointer;
}
.insta-icon {
  padding-left: 5px;
  padding-right: 5px;
}
input,
input::-webkit-input-placeholder {
  font-size: 14px;
}
.footer-heading {
  display: inline-block;
  letter-spacing: 0.06em;
}
.footer-para {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.04em;
}
.footerLogo{
  width: 40%;
  margin-bottom: 2px;
}
.search-label {
  font-size: 18px;
  font-weight: 500;
  line-height: 30px;
}
.sugg-para {
  font-size: 15px;
  font-weight: 500;
  opacity: 0.7;
}
.home-para{
  margin-top: 25px;
  width: 96%;
}
.home-para p{
  font-size: 14px;
  line-height: 20px;
}

.ant-input{
    background: #FAFAFA;
}
.ant-input-affix-wrapper{
  position: relative;
  background: #FAFAFA;  
}
.ant-input-prefix{
    position: absolute;
    top: 12px;
    right: 12px;
    z-index: 1;
    opacity: 0.5;
}
.ant-input-prefix:hover{
    color: #2685d3;
    cursor: pointer;
}
.textarea{
    margin-top: 30px;
}

.menus-main {
  padding: 0;
  margin-top: 30px;
}
.ant-menu-inline {
  border: none;
}
.ant-menu-sub.ant-menu-inline {
  border: solid 1px rgb(239, 239, 239);
  background-color: white;
}
.ant-menu-item a {
  color: #0094ff;
  font-size: 14px;
  letter-spacing: 0.05em;
}
.ant-menu-inline .ant-menu-submenu {
  background: #0094ff;
  margin-top: 5px;
}
.ant-menu-submenu-title {
  font-size: 16px;
  color: white;
  font-weight: 600;
}
.ant-menu-submenu-title:hover {
  color: rgb(255, 255, 255);
  -webkit-text-decoration: underline #4ab4ff;
          text-decoration: underline #4ab4ff;
}
.ant-menu-submenu-arrow {
  color: white;
}

.Contact-content {
  height: 48vh;
  background-image: url(/dtangeeda/hospitals-ui/static/media/contactbg.a9f1b1f6.png);
  background-size: cover;
}
.top-heading {
  text-align: center;
  margin-top: 120px;
  color: white;
}
.enter-data label {
  font-size: 16px;
  line-height: 30px;
}
.enter-data input {
  height: 30px;
  border-radius: 5px;
}
.Terms-details {  
  line-height: 50px;
}
.last-name-div {
  margin-top: 30px;
}
.subject-div {
  margin-top: 30px;
}
.contact-para{
display: inline-block;
}
.linkk{
 margin-left: 4px; 
}
.linkk::after{
  content: "and";
  color: rgb(46, 46, 46);
  margin-left: 3px;
  margin-right: 3px;
  font-size: 14px;
}
.Blog-content {
  background-image: url(/dtangeeda/hospitals-ui/static/media/Blog.293ee857.png);
  background-size: cover;
  background-position: center;
  height: 48vh;
}

/* card Arow icon */
.anticon-arrow-right {
  color: #0094ff;
  margin-top: 5px;
  margin-left: 10px;
}
.cards {
  margin-top: 40px;
}
.Blog-show-More {
  border: none;
  font-size: 18px;
  background: none;
  font-weight: 600;
  opacity: 0.5;
  outline: none;
}
.Blog-show-More:hover {
cursor: pointer;
color: #0094ff;

}
.show-btn-div {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
}

.blog-para {
  line-height: 28px;
  letter-spacing: 0.04em;
}
.blog-artical-heading {
  margin-top: 30px !important;
  font-size: 28px;
}

.up-comming-articles{
  font-family: "Poppins", sans-serif;
  margin-bottom: 25px;
  margin-top: 25px;
  text-align: center;
}
